.copyright__link {
  font-family: @arial;
  font-size: 16px;
  line-height: 0;
  color: @dark-grey;
  text-decoration: none;
  align-items: center;
  display: flex;
  justify-content: space-between;
  margin-top: 22px;

  @media (min-width: @tablet-width) {
    width: 163px;
    padding: 0;
    margin: 0;
  }

  &:hover {
    .copyright__icon {
      color: @green-4;
    }
  }

  &:focus {
    .copyright__icon {
      color: @green-4;
    }
  }

  &:active {
    opacity: 0.3;

    .copyright__icon {
      color: @green-4;
    }
  }
}
